import React from 'react';
import NodeLink from 'components/TabContainer';
import DrugSider from 'components/Sider';

import { Layout, Modal } from 'antd';
import { InfoCircleOutlined, LinkOutlined } from '@ant-design/icons';
import './App.css';
import { StateConsumer } from 'stores';
import { IState, IDispatch } from 'types';
import { ACTION_TYPES, selectDisease, selectDrug, selectDrugPrimary, selectDiseaseSecondary, changeAppMode } from 'stores/actions';
import {
  requestNodeTypes,
  requestEdgeTypes,
  requestNodeNameDict,
  requestDiseaseOptions,
  requestDrugList,
} from 'stores/DataService';
import { setNodeColor, LOADING_ICON, INIT_DRUG_PRIMARY, INIT_DISEASES, DEFAULT_APP_MODE } from 'helpers';

const { Header, Footer, Content } = Layout;

interface Props {
  globalState: IState;
  dispatch: IDispatch;
}
interface State {
  width: number;
  height: number;
  isModalVisible: boolean;
}

class App extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      width: window.innerWidth,
      height: window.innerHeight,
      isModalVisible: false,
    };

    this.updateSize = this.updateSize.bind(this);
  }

  showModal = () => {
    this.setState({ isModalVisible: true });
  };

  hideModal = () => {
    this.setState({ isModalVisible: false });
  };

  updateSize() {
    this.setState({
      width: window.innerWidth,
      height: window.innerHeight,
    });
  }

  componentDidMount() {
    window.addEventListener('resize', this.updateSize);

    requestNodeTypes()
      .then((nodeTypes) => {
        setNodeColor(nodeTypes);
        this.props.dispatch({
          type: ACTION_TYPES.Load_Node_Types,
          payload: { nodeTypes },
        });
      })
      .then(() => requestEdgeTypes())
      .then((edgeTypes) => {
        this.props.dispatch({
          type: ACTION_TYPES.Load_Edge_Types,
          payload: { edgeTypes },
        });
      })
      .then(() => requestNodeNameDict())
      .then((nodeNameDict) => {
        this.props.dispatch({
          type: ACTION_TYPES.Load_Node_Name_Dict,
          payload: { nodeNameDict },
        });
      })
      .then(() => requestDiseaseOptions())
      .then((diseaseOptions) => {
        this.props.dispatch({
          type: ACTION_TYPES.Load_Disease_Options,
          payload: { diseaseOptions },
        });
      })
      .then(() => requestDrugList())
      .then((drugList) => {
        // Load available drugs for selection
        const drugOptions = drugList.map(d => d.id);
        this.props.dispatch({
          type: ACTION_TYPES.Load_Drug_List,
          payload: { drugOptions },
        });
      })
      // Initialize in drug-to-disease mode with Inebilizumab
      .then(() => {
        changeAppMode(DEFAULT_APP_MODE, this.props.dispatch);
      })
      .then(() => selectDrugPrimary(INIT_DRUG_PRIMARY, this.props.dispatch))
      .then(() => {
        // Select first disease prediction
        if (INIT_DISEASES.length > 0) {
          selectDiseaseSecondary(INIT_DISEASES[0], INIT_DRUG_PRIMARY, true, this.props.dispatch);
        }
      })
      .then(() => {
        this.props.dispatch({
          type: ACTION_TYPES.Set_Loading_Status,
          payload: { isInitializing: false },
        });
      });
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.updateSize);
  }

  render() {
    let siderWidth = 380,
      footHeight = 20,
      mainViewWidth = Math.max(0, window.innerWidth - siderWidth),
      headerHeight = 64,
      mainViewHeight = Math.max(0, window.innerHeight - headerHeight - footHeight);

    const { isInitializing } = this.props.globalState;

    let header = (
      <Header className="header" style={{ height: headerHeight }}>
        <img
          src="favicon.ico"
          style={{ height: headerHeight * 0.8, padding: '2px 10px' }}
          alt="logo"
        />
        <b style={{ fontSize: '30px' }}>Amgen Drugs and candidate Diseases Explorer</b>
        <span
          style={{ float: 'right', cursor: 'pointer' }}
          onClick={this.showModal}
        >
          <InfoCircleOutlined
            style={{ height: headerHeight, fontSize: '20px' }}
          />
          <span
            style={{
              paddingLeft: '5px',
              fontSize: '18px',
              height: headerHeight,
            }}
          >
            About
          </span>{' '}
        </span>
      </Header>
    );

    return (
      <>
        <Layout>
          {header}

          <Layout>
            <DrugSider siderWidth={siderWidth} />
            <Content className="main" style={{ height: mainViewHeight }}>
              <NodeLink width={mainViewWidth} height={mainViewHeight - footHeight} />
              <Footer
                style={{
                  textAlign: 'center',
                  color: 'gray',
                  height: footHeight,
                  padding: 0,
                }}
              >

                <span style={{ color: 'gray' }}>
                  {' '}
                  <b onClick={this.showModal} style={{ cursor: 'help' }}>
                    DISCLAIMER:
                  </b>
                  THIS WEBSITE DOES NOT PROVIDE MEDICAL ADVICE{' '}
                </span>
              </Footer>
            </Content>
          </Layout>
        </Layout>

        {isInitializing && (
          <svg
            width={window.innerWidth}
            height={window.innerHeight}
            style={{ position: 'absolute', left: 0, top: 0 }}
          >
            <g className="loading">
              <rect
                className="loading__background"
                width={window.innerWidth}
                height={window.innerHeight}
                fill="white"
                opacity={0.5}
              />
              <g
                transform={`translate(${window.innerWidth / 2}, ${window.innerHeight / 2
                  })`}
              >
                {LOADING_ICON}
              </g>
            </g>
          </svg>
        )}

        <Modal
          title="About Amgen Drugs and candidate Diseases Explorer"
          className="about-modal"
          visible={this.state.isModalVisible}
          footer={null}
          onCancel={this.hideModal}
          zIndex={1099}
          width={window.innerWidth * 0.5}
        >
          <h3>About</h3>
          <p>
            This Drug and Diseases Explorer provides a visual interface to interact with the predictions and explanations of an AI model that identify candidate diseases with limited treatment options and minimal molecular understanding for a given Drug.
          </p>
          <p>
            AI model is a graph neural network pre-trained on a comprehensive knowledge graph of 17,080 clinically-recognized diseases and 7,957 therapeutic candidates. The model can process various therapeutic tasks in a unified formulation. Once trained, the model can perform zero-shot inference on new diseases without the need for additional parameters or fine-tuning on ground truth labels.
          </p>

          <h3>Medical Advice Disclaimer</h3>
          <p>
            DISCLAIMER: THIS WEBSITE DOES NOT PROVIDE MEDICAL ADVICE
            <br />
            The information, including but not limited to, text, graphics, images and other material contained on this website are for informational purposes only. No material on this site is intended to be a substitute for professional medical advice, diagnosis or treatment. Always seek the advice of your physician or other qualified health care provider with any questions you may have regarding a medical condition or treatment and before undertaking a new health care regimen, and never disregard professional medical advice or delay in seeking it because of something you have read on this website.
          </p>
        </Modal>
      </>
    );
  }
}

export default StateConsumer(App);
