import React from 'react';
import { requestEmbedding } from 'stores/DataService';
import { IState, IDispatch } from 'types';
import { StateConsumer } from 'stores';
import * as d3 from 'd3';
import { HIGHLIGHT_COLOR, SELECTED_COLOR } from 'helpers/color';
import { LOADING_ICON } from 'helpers';
import { selectDrug, selectDrugPrimary, selectDiseaseSecondary } from 'stores/actions';
import { isAddDrug, isAddDisease } from 'stores/reducer';
import lasso from './lasso.js';

// Disease category colors
const CATEGORY_COLORS: { [key: string]: string } = {
  cancer: '#e53935',          // Red
  neurological: '#8e24aa',    // Purple
  cardiovascular: '#d81b60',  // Pink
  autoimmune: '#f4511e',      // Deep Orange
  infectious: '#43a047',      // Green
  metabolic: '#1e88e5',       // Blue
  respiratory: '#00acc1',     // Cyan
  gastrointestinal: '#fb8c00', // Orange
  genetic: '#5e35b1',         // Deep Purple
  other: '#90a4ae',           // Gray
};

interface EmbeddingData {
  coords: [number, number];
  category: string;
  name: string;
  predicted: boolean;
}

interface State {
  embedding: { [key: string]: [number, number] | EmbeddingData };
  tooltip: {
    visible: boolean;
    info: string;
    position: [number, number];
  };
  showCategoryColors: boolean;
}
interface Props {
  width: number;
  height: number;
  globalState: IState;
  dispatch: IDispatch;
}

class Scatter extends React.Component<Props, State> {
  circleRadius = 3;
  hoverTimeout: number = 0;
  WAIT = 500;
  constructor(props: Props) {
    super(props);
    this.state = {
      embedding: {},
      tooltip: { visible: false, info: '', position: [0, 0] },
      showCategoryColors: true,  // Enable category coloring by default
    };
    this.onChangeDrug = this.onChangeDrug.bind(this);
  }
  
  // Helper to get coordinates from embedding data (supports both old and new format)
  getCoords(data: [number, number] | EmbeddingData): [number, number] {
    if (Array.isArray(data)) {
      return data;
    }
    return data.coords;
  }
  
  // Helper to get category from embedding data
  getCategory(data: [number, number] | EmbeddingData): string {
    if (Array.isArray(data)) {
      return 'other';
    }
    return data.category || 'other';
  }
  
  // Helper to get name from embedding data
  getName(data: [number, number] | EmbeddingData): string {
    if (Array.isArray(data)) {
      return '';
    }
    return data.name || '';
  }
  async loadEmbedding() {
    const { selectedDrugPrimary } = this.props.globalState;
    const embedding = await requestEmbedding(selectedDrugPrimary);
    this.setState({ embedding });
  }
  componentDidMount() {
    this.loadEmbedding();
  }
  componentDidUpdate(prevProps: Props) {
    // Reload embeddings when the primary drug changes
    const { selectedDrugPrimary } = this.props.globalState;
    const prevDrugPrimary = prevProps.globalState.selectedDrugPrimary;
    
    if (selectedDrugPrimary !== prevDrugPrimary) {
      this.loadEmbedding();
    }
  }

  addLasso(width: number, height: number) {
    const { drugPredictions } = this.props.globalState;
    const selectedDrugIds = drugPredictions
      .filter((d) => d.selected)
      .map((d) => d.id);
    // lasso draw
    d3.selectAll('g.lasso').remove();
    var svg = d3.select('svg.scatter');

    var lasso_area = d3.select('rect.lasso');

    // Lasso functions to execute while lassoing
    var lasso_start = () => {
      // (mylasso.items() as any).attr('r', 5); // reset size
      // .attr('fill', 'white')
    };

    var lasso_draw = () => {
      // Style the possible dots
      // mylasso
      // .possibleItems()
      // .classed("possible", true)
    };

    var lasso_end = () => {
      // mylasso.selectedItems()
      //     .attr('fill', colors[this.selected.length])
      //     .attr('r', '7')
      //     .classed(`group_${this.selected.length}`, true)
      // mylasso
      // .items()
      // .classed("possible", false)

      (mylasso.selectedItems() as any)._groups[0].forEach((d: any) => {
        const drugID = d.attributes.id.value;
        if (!selectedDrugIds.includes(drugID)) {
          selectDrug(
            drugID,
            this.props.globalState.selectedDisease,
            true,
            this.props.dispatch
          );
        }
      });
    };

    var mylasso = lasso();
    mylasso.items(svg.selectAll('circle.highlighted'));
    mylasso
      .targetArea(lasso_area) // area where the lasso can be started
      .on('start', lasso_start) // lasso start function
      .on('draw', lasso_draw) // lasso draw function
      .on('end', lasso_end); // lasso end function

    svg.call(mylasso);
  }
  drawScatter() {
    const { diseasePredictions, appMode } = this.props.globalState;
    const { width, height } = this.props;

    // this.addLasso(width, height);

    const { embedding, showCategoryColors } = this.state;
    
    // Extract coordinates for domain calculation
    const allCoords = Object.values(embedding).map((d) => this.getCoords(d));
    const xDomain = d3.extent(allCoords.map((d) => d[0])) as [number, number];
    const yDomain = d3.extent(allCoords.map((d) => d[1])) as [number, number];

    const xScale = d3
      .scaleLinear()
      .domain(xDomain)
      .range([this.circleRadius, width - this.circleRadius]);
    const yScale = d3
      .scaleLinear()
      .domain(yDomain)
      .range([this.circleRadius, height - this.circleRadius]);

    // Use disease predictions in drug-to-disease mode
    const diseaseIds = diseasePredictions.map((d) => d.node_id || d.id);
    const selectedDiseaseIds = diseasePredictions
      .filter((d) => d.selected)
      .map((d) => d.node_id || d.id);

    const getSortIndex = (diseaseId: string) =>
      diseaseIds.indexOf(diseaseId) > -1 ? diseaseIds.indexOf(diseaseId) : diseaseIds.length;

    const nodes = Object.keys(embedding)
      .sort((a, b) => -getSortIndex(a) + getSortIndex(b))
      .map((diseaseId) => {
        const embData = embedding[diseaseId];
        const [x, y] = this.getCoords(embData);
        const category = this.getCategory(embData);
        const diseaseName = this.getName(embData);
        
        const diseaseRank = diseaseIds.indexOf(diseaseId);
        const isHighlighted = diseaseRank > -1; // predicted diseases
        const isSelected = selectedDiseaseIds.includes(diseaseId);
        
        // Determine fill color based on selection, highlighting, and category
        let fillColor: string;
        if (isSelected) {
          fillColor = SELECTED_COLOR;
        } else if (isHighlighted) {
          fillColor = showCategoryColors 
            ? (CATEGORY_COLORS[category] || HIGHLIGHT_COLOR)
            : HIGHLIGHT_COLOR;
        } else {
          fillColor = showCategoryColors 
            ? (CATEGORY_COLORS[category] || 'lightGray')
            : 'lightGray';
        }

        return (
          <circle
            cx={xScale(x)}
            cy={yScale(y)}
            key={diseaseId}
            className={isHighlighted ? 'highlighted disease' : 'disease'}
            id={diseaseId}
            fill={fillColor}
            opacity={isSelected ? 1 : isHighlighted ? 0.85 : 0.5}
            stroke={isSelected ? '#fff' : 'rgba(255,255,255,0.5)'}
            strokeWidth={isSelected ? 2 : 1}
            r={
              isSelected
                ? this.circleRadius * 2
                : isHighlighted
                  ? this.circleRadius * 1.3
                  : this.circleRadius
            }
            onDoubleClick={() => {
              if (isHighlighted) this.onChangeDisease(diseaseId);
            }}
            onClick={() => this.showTooltip(diseaseId, [xScale(x), yScale(y)], category, diseaseName)}
          />
        );
      });
    return nodes;
  }
  onChangeDisease(selectedDiseaseNodeId: string) {
    const { appMode, selectedDrugPrimary, diseasePredictions } = this.props.globalState;

    if (appMode === 'drug-to-disease' && selectedDrugPrimary) {
      // Find the disease prediction by node_id to get the correct disease_id
      const diseasePrediction = diseasePredictions.find(
        (d) => (d.node_id || d.id) === selectedDiseaseNodeId
      );
      
      if (!diseasePrediction) {
        console.warn(`Disease prediction not found for node_id: ${selectedDiseaseNodeId}`);
        return;
      }
      
      // Use disease_id (d.id) for selection toggle, node_id for attention API
      const diseaseId = diseasePrediction.id;
      const diseaseNodeId = diseasePrediction.node_id || diseasePrediction.id;
      
      // In Drug-to-Disease mode, clicking a disease selects/deselects it
      const isAdd = isAddDisease(diseasePredictions, diseaseId);
      selectDiseaseSecondary(
        diseaseId,           // disease_id for selection toggle
        selectedDrugPrimary,
        isAdd,
        this.props.dispatch,
        diseaseNodeId        // node_id for attention API
      );
    }
  }
  onChangeDrug(selectedDrug: string) {
    const { appMode } = this.props.globalState;

    if (appMode === 'drug-to-disease') {
      selectDrugPrimary(selectedDrug, this.props.dispatch);
    } else {
      const isAdd = isAddDrug(
        this.props.globalState.drugPredictions,
        selectedDrug
      );
      selectDrug(
        selectedDrug,
        this.props.globalState.selectedDisease,
        isAdd,
        this.props.dispatch
      );
    }
  }
  showTooltip(diseaseId: string, position: [number, number], category?: string, name?: string) {
    const { nodeNameDict } = this.props.globalState;
    this.hoverTimeout = window.setTimeout(() => {
      const diseaseName = name || nodeNameDict['disease']?.[diseaseId] || diseaseId;
      const categoryLabel = category ? ` [${category}]` : '';
      this.setState({
        tooltip: {
          visible: true,
          info: `${diseaseName}${categoryLabel}`,
          position,
        },
      });
    }, this.WAIT);
  }
  hideTooltip() {
    window.clearTimeout(this.hoverTimeout);
    this.setState({
      tooltip: {
        visible: false,
        info: ``,
        position: [0, 0],
      },
    });
  }
  render() {
    const { isDrugLoading, isInitializing } = this.props.globalState;
    const { width, height } = this.props;
    const { tooltip, showCategoryColors } = this.state;

    return (
      <div style={{ position: 'relative' }}>
        <svg width={width} height={height} className="scatter">
          <rect
            className="lasso area"
            width={width}
            height={height}
            opacity={0}
          />
          <g className="scatter">
            {this.state.embedding ? (
              this.drawScatter()
            ) : (
              <g
                transform={`translate(${width / 2}, ${height / 2})`}
                textAnchor="middle"
              >
                {LOADING_ICON}
              </g>
            )}
            {/* overlap a loading icon when loading */}
            {isDrugLoading && !isInitializing ? (
              <g
                transform={`translate(${width / 2}, ${height / 2})`}
                textAnchor="middle"
              >
                {LOADING_ICON}
              </g>
            ) : (
              <g />
            )}
          </g>{' '}
          <text x={0} y={height - 25} fontSize={12} fill="gray">
            Click to reveal the disease name.
          </text>
          <text x={0} y={height - 10} fontSize={12} fill="gray">
            Double click to select the disease.
          </text>
        </svg>
        
        {/* Category Legend */}
        <div
          style={{
            position: 'absolute',
            top: 5,
            right: 5,
            background: 'rgba(255,255,255,0.95)',
            padding: '8px',
            borderRadius: '6px',
            boxShadow: '0 1px 4px rgba(0,0,0,0.15)',
            fontSize: '10px',
            maxWidth: '140px',
          }}
        >
          <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '6px' }}>
            <span style={{ fontWeight: 'bold', fontSize: '11px' }}>Categories</span>
            <button
              onClick={() => this.setState({ showCategoryColors: !showCategoryColors })}
              style={{
                padding: '2px 6px',
                fontSize: '9px',
                border: '1px solid #ccc',
                borderRadius: '3px',
                background: showCategoryColors ? '#e3f2fd' : '#fff',
                cursor: 'pointer',
              }}
            >
              {showCategoryColors ? 'ON' : 'OFF'}
            </button>
          </div>
          {showCategoryColors && (
            <div style={{ display: 'flex', flexWrap: 'wrap', gap: '4px' }}>
              {Object.entries(CATEGORY_COLORS).slice(0, 9).map(([category, color]) => (
                <div key={category} style={{ display: 'flex', alignItems: 'center', gap: '3px', width: '48%' }}>
                  <div
                    style={{
                      width: 8,
                      height: 8,
                      borderRadius: '50%',
                      backgroundColor: color,
                    }}
                  />
                  <span style={{ textTransform: 'capitalize', fontSize: '9px' }}>
                    {category.substring(0, 8)}
                  </span>
                </div>
              ))}
            </div>
          )}
        </div>
        
        <div
          className={`tooltip tooltip-${tooltip.visible ? 'show' : 'hide'}`}
          style={{
            left: tooltip.position[0] + this.circleRadius,
            top: tooltip.position[1] + this.circleRadius,
          }}
        >
          {tooltip.info}
        </div>
      </div>
    );
  }
}

export default StateConsumer(Scatter);
