const requestDrugPredictions = async (diseaseID: string) => {
  const url = `./api/drug_predictions?disease_id=${diseaseID}`;
  const response = await axiosInstance.get(url);
  const predictions = response.data;
  return predictions;
};

const requestDiseasePredictions = async (drugID: string) => {
  const url = `./api/disease_predictions?drug_id=${drugID}&top_n=30`;
  const response = await axiosInstance.get(url);
  const predictions = response.data;
  return predictions;
};

const requestEmbedding = async (drugId?: string) => {
  // Request drug-specific disease embeddings from API
  const url = drugId 
    ? `./api/disease_embeddings?drug_id=${drugId}`
    : './data/disease_tsne.json';
  const response = await axiosInstance.get(url);
  return response.data;
};

const requestDrugList = async (): Promise<{id: string, name: string}[]> => {
  // Get list of available drugs from API
  const url = './api/drug_list';
  const response = await axiosInstance.get(url);
  return response.data;
};

interface SubgraphNode {
  id: string;
  label: string;
  type: string;
}

interface SubgraphEdge {
  source: string;
  target: string;
  relation: string;
  layer1_att: number;
  layer2_att: number;
  attention: number;
}

interface Subgraph {
  nodes: SubgraphNode[];
  edges: SubgraphEdge[];
}

const requestSubgraph = async (drugId: string, diseaseId: string): Promise<Subgraph> => {
  // Get subgraph for a drug-disease pair from API
  const url = `./api/subgraph?drug_id=${drugId}&disease_id=${diseaseId}`;
  const response = await axiosInstance.get(url);
  return response.data;
};

export {
  requestNodeTypes,
  requestEdgeTypes,
  requestAttention,
  requestNodeNameDict,
  requestDrugPredictions,
  requestDiseaseOptions,
  requestEmbedding,
  requestAttentionPair,
  requestDiseasePredictions,
  requestDrugList,
  requestSubgraph,
};
