import * as React from 'react';
import * as d3 from 'd3';
import { IState, IDispatch } from 'types';
import { LOADING_ICON } from 'helpers';
import { requestSubgraph } from 'stores/DataService';

interface Props {
  height: number;
  width: number;
  globalState: IState;
  dispatch: IDispatch;
}

interface SubgraphEdge {
  source: string | SimNode;
  target: string | SimNode;
  relation: string;
  layer1_att: number;
  layer2_att: number;
  attention: number;
  on_path?: boolean;  // For path highlighting
}

interface SubgraphNode {
  id: string;
  label: string;
  type: string;
  on_path?: boolean;  // For path highlighting
  category?: string;  // Disease category
}

interface SimNode extends d3.SimulationNodeDatum {
  id: string;
  label: string;
  type: string;
  on_path?: boolean;
  category?: string;
}

interface Subgraph {
  nodes: SubgraphNode[];
  edges: SubgraphEdge[];
}

interface State {
  subgraph: Subgraph | null;
  loading: boolean;
  error: string | null;
}

// Node colors matching V1 dashboard
const NODE_COLORS: { [key: string]: string } = {
  drug: '#3a7bd5',
  disease: '#dc3545',
  'gene/protein': '#28a745',
  protein: '#28a745',
  pathway: '#fd7e14',
  biological_process: '#6f42c1',
  molecular_function: '#20c997',
  cellular_component: '#e83e8c',
  anatomy: '#17a2b8',
  effect_phenotype: '#ffc107',
  exposure: '#795548',
  unknown: '#6c757d',
};

export default class V1SubGraph extends React.Component<Props, State> {
  svgRef = React.createRef<SVGSVGElement>();
  simulation: d3.Simulation<SimNode, SubgraphEdge> | null = null;
  selectedDrugPrimary: string = '';
  diseaseNodeId: string = '';

  constructor(props: Props) {
    super(props);
    this.state = {
      subgraph: null,
      loading: false,
      error: null,
    };
    this.dragstarted = this.dragstarted.bind(this);
    this.dragged = this.dragged.bind(this);
    this.dragended = this.dragended.bind(this);
  }

  componentDidMount() {
    this.loadSubgraph();
  }

  componentDidUpdate(prevProps: Props) {
    const { selectedDrugPrimary, diseasePredictions } = this.props.globalState;
    const prevDrug = prevProps.globalState.selectedDrugPrimary;
    const prevDiseases = prevProps.globalState.diseasePredictions;

    // Get currently selected disease
    const selectedDisease = diseasePredictions.find((d) => d.selected);
    const prevSelectedDisease = prevDiseases.find((d) => d.selected);

    if (
      selectedDrugPrimary !== prevDrug ||
      selectedDisease?.id !== prevSelectedDisease?.id
    ) {
      this.loadSubgraph();
    }
  }

  componentWillUnmount() {
    if (this.simulation) {
      this.simulation.stop();
    }
  }

  async loadSubgraph() {
    const { selectedDrugPrimary, diseasePredictions } = this.props.globalState;
    const selectedDisease = diseasePredictions.find((d) => d.selected);

    if (!selectedDrugPrimary || !selectedDisease) {
      this.setState({ subgraph: null, loading: false });
      return;
    }

    const diseaseNodeId = selectedDisease.node_id || selectedDisease.id;
    this.selectedDrugPrimary = selectedDrugPrimary;
    this.diseaseNodeId = diseaseNodeId;

    this.setState({ loading: true, error: null });

    try {
      const subgraph = await requestSubgraph(selectedDrugPrimary, diseaseNodeId);
      this.setState({ subgraph, loading: false }, () => {
        this.drawGraph();
      });
    } catch (error) {
      console.error('Failed to load subgraph:', error);
      this.setState({ loading: false, error: 'Failed to load subgraph data' });
    }
  }

  getNodeColor(type: string): string {
    return NODE_COLORS[type] || NODE_COLORS['unknown'];
  }

  dragstarted(d: SimNode) {
    if (!d3.event.active) this.simulation?.alphaTarget(0.3).restart();
    d.fx = d.x;
    d.fy = d.y;
  }

  dragged(d: SimNode) {
    d.fx = d3.event.x;
    d.fy = d3.event.y;
  }

  dragended(d: SimNode) {
    if (!d3.event.active) this.simulation?.alphaTarget(0);
    d.fx = null;
    d.fy = null;
  }

  drawGraph() {
    const { subgraph } = this.state;
    const { width, height } = this.props;

    if (!subgraph || !this.svgRef.current) return;

    const selectedDrugPrimary = this.selectedDrugPrimary;
    const diseaseNodeId = this.diseaseNodeId;

    // Clear previous content
    const svg = d3.select(this.svgRef.current);
    svg.selectAll('*').remove();

    // Create deep copies of nodes and edges for D3 simulation
    const nodes: SimNode[] = subgraph.nodes.map((n) => ({
      ...n,
      x: undefined,
      y: undefined,
    }));
    const edges: SubgraphEdge[] = subgraph.edges.map((e) => ({
      ...e,
      source: e.source,
      target: e.target,
    }));

    // Create container for zoom
    const container = svg.append('g').attr('class', 'zoom-container');

    // Create zoom behavior (D3 v5 style)
    const zoom = d3.zoom<SVGSVGElement, unknown>()
      .scaleExtent([0.1, 4])
      .on('zoom', () => {
        container.attr('transform', d3.event.transform);
      });

    svg.call(zoom as any);

    // Create simulation
    if (this.simulation) {
      this.simulation.stop();
    }

    this.simulation = d3
      .forceSimulation<SimNode, SubgraphEdge>(nodes)
      .force(
        'link',
        d3
          .forceLink<SimNode, SubgraphEdge>(edges)
          .id((d) => d.id)
          .distance(120)
      )
      .force('charge', d3.forceManyBody().strength(-250))
      .force('center', d3.forceCenter(width / 2, height / 2))
      .force('collision', d3.forceCollide().radius(40));

    // Draw links - highlight path edges
    const link = container
      .append('g')
      .selectAll('line')
      .data(edges)
      .enter()
      .append('line')
      .attr('stroke', (d: any) => d.on_path ? '#ff5722' : '#999')  // Orange for path edges
      .attr('stroke-opacity', (d: any) => d.on_path ? 1.0 : 0.6)
      .attr('stroke-width', (d: any) => d.on_path ? 4 : Math.max(2, (d.attention || 0.1) * 8))
      .attr('stroke-dasharray', (d: any) => d.on_path ? '8,4' : 'none');

    // Draw nodes - highlight path nodes
    const node = container
      .append('g')
      .selectAll('circle')
      .data(nodes)
      .enter()
      .append('circle')
      .attr('r', (d) => {
        if (d.id === selectedDrugPrimary || d.id === diseaseNodeId) {
          return 25;
        }
        if (d.on_path) {
          return 18;  // Slightly larger for path nodes
        }
        return 15;
      })
      .attr('fill', (d) => this.getNodeColor(d.type))
      .attr('stroke', (d) => {
        if (d.id === selectedDrugPrimary || d.id === diseaseNodeId) {
          return '#ffc107';  // Gold for selected
        }
        if (d.on_path) {
          return '#ff5722';  // Orange for path nodes
        }
        return 'white';
      })
      .attr('stroke-width', (d) => {
        if (d.id === selectedDrugPrimary || d.id === diseaseNodeId) {
          return 4;
        }
        if (d.on_path) {
          return 3;
        }
        return 1.5;
      })
      .attr('cursor', 'pointer')
      .call(
        d3.drag<SVGCircleElement, SimNode>()
          .on('start', this.dragstarted)
          .on('drag', this.dragged)
          .on('end', this.dragended) as any
      );

    // Add node labels
    const label = container
      .append('g')
      .selectAll('text')
      .data(nodes)
      .enter()
      .append('text')
      .attr('font-size', (d) => {
        if (d.id === selectedDrugPrimary || d.id === diseaseNodeId) {
          return '14px';
        }
        return '11px';
      })
      .attr('font-weight', (d) => {
        if (d.id === selectedDrugPrimary || d.id === diseaseNodeId) {
          return 'bold';
        }
        return 'normal';
      })
      .attr('text-anchor', 'middle')
      .attr('dy', (d) => {
        if (d.id === selectedDrugPrimary || d.id === diseaseNodeId) {
          return -30;
        }
        return -20;
      })
      .attr('pointer-events', 'none')
      .text((d) => {
        const maxLen = d.id === selectedDrugPrimary || d.id === diseaseNodeId ? 40 : 25;
        return d.label && d.label.length > maxLen
          ? d.label.substring(0, maxLen) + '...'
          : d.label || d.id;
      });

    // Hover effects (D3 v5 style)
    node
      .on('mouseover', function() {
        d3.select(this).attr('stroke', '#ffc107').attr('stroke-width', 3);
      })
      .on('mouseout', function(d: SimNode) {
        d3.select(this)
          .attr('stroke', d.id === selectedDrugPrimary || d.id === diseaseNodeId ? '#ffc107' : 'white')
          .attr('stroke-width', d.id === selectedDrugPrimary || d.id === diseaseNodeId ? 3 : 1.5);
      });

    // Tooltip
    node.append('title').text((d) => `${d.label}\nType: ${d.type}\nID: ${d.id}`);

    // Update positions on tick
    this.simulation.on('tick', () => {
      link
        .attr('x1', (d: any) => d.source.x)
        .attr('y1', (d: any) => d.source.y)
        .attr('x2', (d: any) => d.target.x)
        .attr('y2', (d: any) => d.target.y);

      node.attr('cx', (d) => d.x || 0).attr('cy', (d) => d.y || 0);

      label.attr('x', (d) => d.x || 0).attr('y', (d) => d.y || 0);
    });

    // Initial zoom to fit
    setTimeout(() => {
      const bounds = (container.node() as SVGGElement)?.getBBox();
      if (bounds) {
        const dx = bounds.width;
        const dy = bounds.height;
        const x = bounds.x + dx / 2;
        const y = bounds.y + dy / 2;
        const scale = Math.min(0.9, 0.9 / Math.max(dx / width, dy / height));
        const translate = [width / 2 - scale * x, height / 2 - scale * y];

        svg.transition()
          .duration(750)
          .call(
            zoom.transform as any,
            d3.zoomIdentity.translate(translate[0], translate[1]).scale(scale)
          );
      }
    }, 500);
  }

  render() {
    const { width, height } = this.props;
    const { subgraph, loading, error } = this.state;
    const { selectedDrugPrimary, diseasePredictions, nodeNameDict } = this.props.globalState;

    const selectedDisease = diseasePredictions.find((d) => d.selected);

    if (loading) {
      return (
        <div
          style={{
            width,
            height,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
          }}
        >
          <svg width={100} height={100}>
            {LOADING_ICON}
          </svg>
        </div>
      );
    }

    if (!selectedDrugPrimary || !selectedDisease) {
      return (
        <div
          style={{
            width,
            height,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            color: '#6c757d',
            fontSize: '16px',
          }}
        >
          Please select a drug and disease to see the knowledge subgraph
        </div>
      );
    }

    if (error) {
      return (
        <div
          style={{
            width,
            height,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            color: '#dc3545',
            fontSize: '16px',
          }}
        >
          {error}
        </div>
      );
    }

    const drugName = nodeNameDict['drug']?.[selectedDrugPrimary] || selectedDrugPrimary;
    const diseaseName = selectedDisease.name || selectedDisease.id;

    return (
      <div style={{ width, height, position: 'relative' }}>
        {/* Info overlay */}
        <div
          style={{
            position: 'absolute',
            top: 10,
            left: 10,
            background: 'rgba(255,255,255,0.95)',
            padding: '10px 15px',
            borderRadius: '8px',
            boxShadow: '0 2px 8px rgba(0,0,0,0.15)',
            zIndex: 10,
            fontSize: '13px',
          }}
        >
          <div style={{ fontWeight: 'bold', color: '#3a7bd5', marginBottom: '5px' }}>
            Knowledge Sub-Graph
          </div>
          <div>
            <strong>Drug:</strong> {drugName}
          </div>
          <div>
            <strong>Disease:</strong> {diseaseName}
          </div>
          {subgraph && (
            <div style={{ color: '#6c757d', marginTop: '5px' }}>
              {subgraph.nodes.length} nodes, {subgraph.edges.length} edges
            </div>
          )}
        </div>

        {/* Legend */}
        <div
          style={{
            position: 'absolute',
            bottom: 10,
            left: 10,
            background: 'rgba(255,255,255,0.95)',
            padding: '10px',
            borderRadius: '8px',
            boxShadow: '0 2px 8px rgba(0,0,0,0.15)',
            zIndex: 10,
            fontSize: '11px',
          }}
        >
          <div style={{ fontWeight: 'bold', marginBottom: '5px' }}>Node Types</div>
          <div style={{ display: 'flex', flexWrap: 'wrap', gap: '8px' }}>
            {Object.entries(NODE_COLORS).slice(0, 8).map(([type, color]) => (
              <div key={type} style={{ display: 'flex', alignItems: 'center', gap: '4px' }}>
                <div
                  style={{
                    width: 12,
                    height: 12,
                    borderRadius: '50%',
                    backgroundColor: color,
                  }}
                />
                <span>{type.replace('_', ' ')}</span>
              </div>
            ))}
          </div>
          <div style={{ fontWeight: 'bold', marginTop: '10px', marginBottom: '5px' }}>Path Highlighting</div>
          <div style={{ display: 'flex', gap: '10px' }}>
            <div style={{ display: 'flex', alignItems: 'center', gap: '4px' }}>
              <div style={{ width: 20, height: 3, backgroundColor: '#ff5722', borderRadius: '2px' }} />
              <span>Shortest path</span>
            </div>
            <div style={{ display: 'flex', alignItems: 'center', gap: '4px' }}>
              <div style={{ width: 12, height: 12, borderRadius: '50%', border: '3px solid #ff5722', backgroundColor: 'transparent' }} />
              <span>Path node</span>
            </div>
          </div>
        </div>

        {/* Instructions */}
        <div
          style={{
            position: 'absolute',
            bottom: 10,
            right: 10,
            background: 'rgba(255,255,255,0.95)',
            padding: '8px 12px',
            borderRadius: '8px',
            boxShadow: '0 2px 8px rgba(0,0,0,0.15)',
            zIndex: 10,
            fontSize: '11px',
            color: '#6c757d',
          }}
        >
          <div> Drag nodes to move</div>
          <div> Scroll to zoom</div>
          <div> Drag background to pan</div>
        </div>

        {/* SVG container */}
        <svg
          ref={this.svgRef}
          width={width}
          height={height}
          style={{
            background: '#f8f9fa',
            borderRadius: '8px',
            border: '2px solid #e9ecef',
          }}
        />
      </div>
    );
  }
}
