import React from 'react';
import { StateConsumer } from 'stores';
import { IDispatch, IState } from 'types';
import {
  ACTION_TYPES,
  selectDisease,
  selectDrug,
  changeAppMode,
  selectDrugPrimary,
  selectDiseaseSecondary,
} from 'stores/actions';

import './Sider.css';

import {
  Col,
  InputNumber,
  Layout,
  Row,
  Select,
  Skeleton,
  Slider,
  Tooltip,
  Radio,
} from 'antd';
import { QuestionCircleOutlined, CheckCircleOutlined } from '@ant-design/icons';
import {
  getNodeColor,
  sigmoid,
  wheterRemoveDisease,
  sentenceCapitalizer,
  INIT_DISEASE,
  INIT_DRUGS,
} from 'helpers';

const { Sider } = Layout;
const { Option } = Select;

interface Props {
  siderWidth: number;
  globalState: IState;
  dispatch: IDispatch;
}

class DrugSider extends React.Component<Props> {
  padding = 10;
  listHeight = 450; // height of the open drug list
  constructor(props: Props) {
    super(props);
    this.changeEdgeTHR = this.changeEdgeTHR.bind(this);
    this.changeRevEdgeTHR = this.changeRevEdgeTHR.bind(this);
    this.onChangeDisease = this.onChangeDisease.bind(this);
    this.onChangeDrug = this.onChangeDrug.bind(this);
    this.onChangeAppMode = this.onChangeAppMode.bind(this);
    this.onChangeDrugPrimary = this.onChangeDrugPrimary.bind(this);
    this.onChangeDiseaseSecondary = this.onChangeDiseaseSecondary.bind(this);
  }

  onChangeAppMode(e: any) {
    changeAppMode(e.target.value, this.props.dispatch);
  }

  onChangeDrugPrimary(selectedDrug: string) {
    selectDrugPrimary(selectedDrug, this.props.dispatch);
  }

  onChangeDiseaseSecondary(selectedDiseases: string[]) {
    // For now, we only support selecting one disease at a time in the secondary list for visualization
    // But the UI might allow multiple. Let's assume the last selected one is the target.
    // We reuse the selectDrug logic but inverted? No, we need to adapt selectDrug or create a new one.
    // Actually, the existing selectDrug logic expects a drug ID.
    // For Drug-to-Disease, we want to visualize the pair.
    // Let's reuse selectDrug but we need to be careful about what 'selectedDrug' means in state.
    // In Drug-to-Disease mode:
    // selectedDrugPrimary is the source drug.
    // We want to see attention for (selectedDrugPrimary, selectedDisease).

    // Wait, the existing visualization expects `selectedDisease` and `drugPredictions` (with `selected` flag).
    // If we want to reuse the visualization components without massive refactoring:
    // We can map:
    // Drug-to-Disease Mode ->
    //   selectedDisease in State = The selected disease from the list (Secondary)
    //   selectedDrug in State = The primary drug (Primary)

    // But `selectedDisease` triggers `requestDrugPredictions`. We don't want that in this mode.
    // We want `selectedDrugPrimary` to trigger `requestDiseasePredictions`.

    // Let's look at `selectDrug` action:
    // It calls `modifyAttentionPaths`.
    // `modifyAttentionPaths` takes (selectedDrug, selectedDisease).

    // So, when a disease is selected in the secondary list:
    // We should call `modifyAttentionPaths(selectedDrugPrimary, selectedDisease)`.

    const prevSelectedDiseases = this.props.globalState.diseasePredictions
      .filter((d) => d.selected)
      .map((d) => d.id);

    const isAdd = selectedDiseases.length > prevSelectedDiseases.length;
    const currentDisease = isAdd
      ? selectedDiseases[selectedDiseases.length - 1]
      : prevSelectedDiseases.filter((d) => !selectedDiseases.includes(d))[0];

    // Trigger attention path update
    if (this.props.globalState.selectedDrugPrimary && currentDisease) {
      const { selectedDrugPrimary } = this.props.globalState;

      // Find the disease prediction to get node_id for attention queries
      const diseasePrediction = this.props.globalState.diseasePredictions.find(
        d => d.id === currentDisease
      );

      // Use node_id if available, otherwise fall back to id
      const diseaseNodeId = diseasePrediction?.node_id || currentDisease;

      // selectDiseaseSecondary handles both the selection toggle and attention path update
      selectDiseaseSecondary(
        currentDisease,  // Pass the id for toggle (matches d.id in diseasePredictions)
        selectedDrugPrimary,
        isAdd,
        this.props.dispatch,
        diseaseNodeId  // Pass node_id for attention API
      );
    }
  }

  onChangeDrug(selectedDrugs: string[]) {
    const prevSelectedDrugs = this.props.globalState.drugPredictions
      .filter((d) => d.selected)
      .map((d) => d.id);
    const isAdd = selectedDrugs.length > prevSelectedDrugs.length;

    const currentDrug = isAdd
      ? selectedDrugs[selectedDrugs.length - 1]
      : prevSelectedDrugs.filter((d) => !selectedDrugs.includes(d))[0];
    selectDrug(
      currentDrug,
      this.props.globalState.selectedDisease,
      isAdd,
      this.props.dispatch
    );
  }
  changeEdgeTHR(value: number | undefined | string) {
    if (typeof value == 'number') {
      this.props.dispatch({
        type: ACTION_TYPES.Change_Edge_THR,
        payload: { edgeThreshold: value },
      });
    }
  }

  changeRevEdgeTHR(value: number | undefined | string) {
    if (typeof value == 'number') {
      this.props.dispatch({
        type: ACTION_TYPES.Change_Edge_THR,
        payload: { edgeThreshold: Math.round(10 - 10 * value) / 10 },
      });
    }
  }

  onChangeDisease(selectedDisease: string) {
    selectDisease(selectedDisease, this.props.dispatch);
  }
  render() {
    let { siderWidth } = this.props;
    let {
      edgeThreshold,
      nodeTypes,
      diseaseOptions,
      drugOptions,
      drugPredictions,
      diseasePredictions,
      nodeNameDict,
      selectedDisease,
      selectedDrugPrimary,
      appMode,
    } = this.props.globalState;

    // Default to disease-to-drug if undefined
    const currentMode = appMode || 'disease-to-drug';

    const defaultDiseaseText = 'Search to Select a disease';
    const defaultDrugText = 'Select a drug from the prediction';
    const selectedDrugIds = drugPredictions
      .filter((d) => d.selected)
      .map((d) => d.id);

    const selectedDiseaseIds = diseasePredictions
      ? diseasePredictions.filter((d) => d.selected).map((d) => d.id)
      : [];

    const untreatable_disease_icon = (
      <Tooltip
        // title="The knowledge graph contains no drug for treating this disease"
        title="Diseases with no known drug indications in the dataset"
      >
        <QuestionCircleOutlined style={{ color: '#eb2f96' }} />
      </Tooltip>
    );

    const known_drug_icon = (
      <Tooltip
        // title="the knowledge graph contains this drug indication"
        title="US FDA-approved drug indication"
      >
        <CheckCircleOutlined style={{ color: '#52c41a' }} />
      </Tooltip>
    );

    let sider = (
      <Sider
        width={siderWidth}
        theme="light"
        style={{ padding: `${this.padding}px` }}
      >
        <div style={{ marginBottom: 10, textAlign: 'center' }}>
          <Radio.Group value={currentMode} onChange={this.onChangeAppMode}>
            <Radio.Button value="disease-to-drug">Disease &rarr; Drug</Radio.Button>
            <Radio.Button value="drug-to-disease">Drug &rarr; Disease</Radio.Button>
          </Radio.Group>
        </div>

        {currentMode === 'disease-to-drug' ? (
          <>
            Disease:
            <Select
              defaultValue={INIT_DISEASE}
              style={{ width: siderWidth - 2 * this.padding }}
              onChange={this.onChangeDisease}
              showSearch
              optionFilterProp="label"
              value={selectedDisease}
            >
              {diseaseOptions.length > 0 ? (
                diseaseOptions
                  .filter(
                    (d) => {
                      if (!nodeNameDict['disease'] || !nodeNameDict['disease'][d[0]]) return true;
                      return !wheterRemoveDisease(nodeNameDict['disease'][d[0]]);
                    }
                  )
                  .map((d) => {
                    const [id, treatable] = d;
                    const name = (nodeNameDict['disease'] && nodeNameDict['disease'][id]) || id;
                    const cropName =
                      name.length * 10 > siderWidth * 0.8
                        ? name.slice(0, 40) + '...'
                        : name;
                    return (
                      <Option value={id} label={name} key={`diseaseID_${d}`}>
                        <span title={name}> {sentenceCapitalizer(cropName)} </span>
                        {!treatable && untreatable_disease_icon}
                      </Option>
                    );
                  })
              ) : (
                <Option value="loading" label="loading" key="loading">
                  data is loading..
                </Option>
              )}
            </Select>
            <br />
            Drug:
            {drugPredictions.length === 0 ? (
              // <span>Data is Loading.. </span>
              <Skeleton />
            ) : (
              <Select
                mode="multiple"
                style={{ width: siderWidth - 2 * this.padding }}
                open
                showSearch
                optionFilterProp="label"
                listHeight={this.listHeight}
                onChange={this.onChangeDrug}
                defaultValue={INIT_DRUGS}
                value={selectedDrugIds}
                // menuItemSelectedIcon={<EyeOutlined />}
                menuItemSelectedIcon={<></>}
              >
                {drugPredictions.map((d, idx) => {
                  const { id: drug_id, score, known } = d;
                  const name = (nodeNameDict['drug'] && nodeNameDict['drug'][drug_id]) || drug_id;
                  const cropName =
                    name.length * 10 > siderWidth * 0.5
                      ? name.slice(0, 20) + '...'
                      : name;
                  return (
                    <Option value={drug_id} key={`disease_${idx}`} label={name}>
                      <div>
                        <span>
                          [{idx + 1}] <span title={name}>{cropName}</span>{' '}
                          {known && known_drug_icon}
                        </span>
                        <span style={{ float: 'right' }}>
                          score: {sigmoid(score).toFixed(3)}
                          {/* rank: {idx + 1} */}
                        </span>
                      </div>
                    </Option>
                  );
                })}
              </Select>
            )}
          </>
        ) : (
          <>
            Drug:
            <Select
              style={{ width: siderWidth - 2 * this.padding }}
              onChange={this.onChangeDrugPrimary}
              showSearch
              optionFilterProp="label"
              placeholder="Click to select a drug"
              value={selectedDrugPrimary}
              dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}
              listHeight={350}
              virtual={false}
            >
              {drugOptions && drugOptions.length > 0 ? (
                // Sort drugs alphabetically by name
                [...drugOptions]
                  .map((drug_id) => ({
                    id: drug_id,
                    name: nodeNameDict['drug'] ? nodeNameDict['drug'][drug_id] : drug_id
                  }))
                  .sort((a, b) => (a.name || a.id).localeCompare(b.name || b.id))
                  .map(({ id: drug_id, name }, idx) => {
                    const displayName = name || drug_id;
                    return (
                      <Option value={drug_id} label={displayName} key={`drugID_${drug_id}`}>
                        <div style={{ padding: '4px 0' }}>
                          <span style={{ fontWeight: 500 }}>[{idx + 1}]</span>{' '}
                          <span title={displayName}>{displayName}</span>
                        </div>
                      </Option>
                    );
                  })
              ) : (
                <Option value="loading" label="loading" key="loading" disabled>
                  Loading drugs... (Please wait)
                </Option>
              )}
            </Select>
            <br />
            Disease Candidates:
            {!diseasePredictions || diseasePredictions.length === 0 ? (
              this.props.globalState.isDiseaseLoading ? <Skeleton /> : <div>Select a drug to see candidates</div>
            ) : (
              <Select
                mode="multiple"
                style={{ width: siderWidth - 2 * this.padding }}
                open
                showSearch
                optionFilterProp="label"
                listHeight={this.listHeight}
                onChange={this.onChangeDiseaseSecondary}
                value={selectedDiseaseIds}
                menuItemSelectedIcon={<></>}
              >
                {diseasePredictions.map((d: any, idx) => {
                  const { id: disease_id, score, known, name: predictionName, percentile, z_score, rank } = d;
                  const name = predictionName || (nodeNameDict['disease'] ? nodeNameDict['disease'][disease_id] : disease_id);
                  const cropName =
                    name && name.length * 10 > siderWidth * 0.5
                      ? name.slice(0, 20) + '...'
                      : name;
                  
                  // Format display info - prefer percentile/rank over raw score
                  const topPercent = percentile ? (100 - percentile).toFixed(2) : null;
                  const displayRank = rank || (idx + 1);
                  const tooltipInfo = `Raw score: ${score?.toFixed(6) || 'N/A'}${z_score ? `, Z-score: ${z_score.toFixed(2)}` : ''}`;
                  
                  return (
                    <Option value={disease_id} key={`disease_${idx}`} label={name}>
                      <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                        <span style={{ flex: 1, overflow: 'hidden', textOverflow: 'ellipsis' }}>
                          <span style={{ color: '#666', marginRight: 4 }}>#{displayRank}</span>
                          <span title={name}>{cropName}</span>{' '}
                          {known && known_drug_icon}
                        </span>
                        <span 
                          style={{ fontSize: '11px', color: '#1890ff', marginLeft: 8 }}
                          title={tooltipInfo}
                        >
                          {topPercent ? `Top ${topPercent}%` : `${sigmoid(score).toFixed(3)}`}
                        </span>
                      </div>
                    </Option>
                  );
                })}
              </Select>
            )}
          </>
        )}

        <div className="dummy" style={{ height: this.listHeight + 20 }} />
        Minimum self-explaining edge score:
        <Row gutter={20}>
          <Col span={5}>
            <InputNumber
              value={edgeThreshold}
              onChange={this.changeEdgeTHR}
              style={{ width: '100%' }}
              step={0.1}
              min={0}
              max={1}
            />
          </Col>
          <Col span={16}>
            <Slider
              step={0.1}
              value={1 - edgeThreshold}
              min={0}
              max={1}
              onChange={this.changeRevEdgeTHR}
              marks={{ 0: '1.0', 1: '0.0' }}
              reverse={true}
              tipFormatter={(value?: number) => {
                return value && (1 - value).toFixed(1);
              }}
            />
          </Col>
        </Row>
        <div className="nodeTypes">
          Node Types:
          <br />
          {nodeTypes.map((nodeType) => {
            return (
              <div key={nodeType} style={{ marginLeft: '5px' }}>
                {/* <input type="checkbox" style={{ margin: "2px" }} /> */}
                <span
                  style={{
                    background: getNodeColor(nodeType),
                    color: 'white',
                    padding: '2px',
                  }}
                >
                  {nodeType}
                </span>
              </div>
            );
          })}
        </div>
        <br />
        {/* <Button
          icon={<SearchOutlined />}
          type="primary"
          shape="round"
          onClick={() => this.startAnalysis()}
        >
          Show Attention Tree
        </Button> */}
        <br />
      </Sider>
    );

    return sider;
  }
}

export default StateConsumer(DrugSider);
