import * as React from 'react';
import { IState, IDispatch } from 'types';
import { LOADING_ICON, getNodeColor } from 'helpers';
import { requestSubgraph } from 'stores/DataService';

interface Props {
  height: number;
  width: number;
  globalState: IState;
  dispatch: IDispatch;
}

interface SubgraphEdge {
  source: string;
  target: string;
  relation: string;
  layer1_att: number;
  layer2_att: number;
  attention: number;
}

interface SubgraphNode {
  id: string;
  label: string;
  type: string;
}

interface Subgraph {
  nodes: SubgraphNode[];
  edges: SubgraphEdge[];
}

interface State {
  subgraph: Subgraph | null;
  loading: boolean;
  error: string | null;
}

export default class V1AttentionTable extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      subgraph: null,
      loading: false,
      error: null,
    };
  }

  componentDidMount() {
    this.loadSubgraph();
  }

  componentDidUpdate(prevProps: Props) {
    const { selectedDrugPrimary, diseasePredictions } = this.props.globalState;
    const prevDrug = prevProps.globalState.selectedDrugPrimary;
    const prevDiseases = prevProps.globalState.diseasePredictions;

    // Get currently selected disease
    const selectedDisease = diseasePredictions.find((d) => d.selected);
    const prevSelectedDisease = prevDiseases.find((d) => d.selected);

    if (
      selectedDrugPrimary !== prevDrug ||
      selectedDisease?.id !== prevSelectedDisease?.id
    ) {
      this.loadSubgraph();
    }
  }

  async loadSubgraph() {
    const { selectedDrugPrimary, diseasePredictions, nodeNameDict } = this.props.globalState;
    const selectedDisease = diseasePredictions.find((d) => d.selected);

    if (!selectedDrugPrimary || !selectedDisease) {
      this.setState({ subgraph: null, loading: false });
      return;
    }

    // Get disease node ID
    const diseaseNodeId = selectedDisease.node_id || selectedDisease.id;

    this.setState({ loading: true, error: null });

    try {
      const subgraph = await requestSubgraph(selectedDrugPrimary, diseaseNodeId);
      this.setState({ subgraph, loading: false });
    } catch (error) {
      console.error('Failed to load subgraph:', error);
      this.setState({ loading: false, error: 'Failed to load attention data' });
    }
  }

  formatType(type: string): string {
    if (!type) return 'Unknown';
    if (type === 'gene/protein') return 'Protein';
    return type
      .replace(/_/g, ' ')
      .replace(/\b\w/g, (l) => l.toUpperCase());
  }

  renderAttentionBar(score: number): React.ReactNode {
    const percentage = Math.min(score * 100, 100);
    const color =
      score > 0.7
        ? '#28a745'
        : score > 0.4
        ? '#ffc107'
        : '#dc3545';

    return (
      <div
        style={{
          width: '100%',
          height: '20px',
          backgroundColor: '#e9ecef',
          borderRadius: '10px',
          overflow: 'hidden',
        }}
      >
        <div
          style={{
            width: `${percentage}%`,
            height: '100%',
            background: `linear-gradient(90deg, ${color} 0%, ${color}88 100%)`,
            borderRadius: '10px',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'flex-end',
            paddingRight: '5px',
          }}
        >
          <span
            style={{
              fontSize: '11px',
              fontWeight: 'bold',
              color: 'white',
              textShadow: '1px 1px 2px rgba(0,0,0,0.5)',
            }}
          >
            {score.toFixed(3)}
          </span>
        </div>
      </div>
    );
  }

  render() {
    const { width, height } = this.props;
    const { subgraph, loading, error } = this.state;
    const { selectedDrugPrimary, diseasePredictions, nodeNameDict } = this.props.globalState;

    const selectedDisease = diseasePredictions.find((d) => d.selected);

    if (loading) {
      return (
        <div
          style={{
            width,
            height,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
          }}
        >
          <svg width={100} height={100}>
            {LOADING_ICON}
          </svg>
        </div>
      );
    }

    if (!selectedDrugPrimary || !selectedDisease) {
      return (
        <div
          style={{
            width,
            height,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            color: '#6c757d',
            fontSize: '16px',
          }}
        >
          Please select a drug and disease to see attention scores
        </div>
      );
    }

    if (error) {
      return (
        <div
          style={{
            width,
            height,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            color: '#dc3545',
            fontSize: '16px',
          }}
        >
          {error}
        </div>
      );
    }

    if (!subgraph || !subgraph.edges || subgraph.edges.length === 0) {
      return (
        <div
          style={{
            width,
            height,
            padding: '20px',
          }}
        >
          <div
            style={{
              background: '#e7f3ff',
              borderLeft: '4px solid #3a7bd5',
              padding: '15px',
              borderRadius: '5px',
            }}
          >
            No attention data found for this drug-disease pair.
          </div>
        </div>
      );
    }

    // Create node lookup map
    const nodeMap: { [key: string]: SubgraphNode } = {};
    subgraph.nodes.forEach((node) => {
      nodeMap[node.id] = node;
    });

    // Group edges by relation
    const edgesByRelation: { [key: string]: SubgraphEdge[] } = {};
    subgraph.edges.forEach((edge) => {
      const relation = edge.relation || 'unknown';
      if (!edgesByRelation[relation]) {
        edgesByRelation[relation] = [];
      }
      edgesByRelation[relation].push(edge);
    });

    // Sort edges within each group by attention score
    Object.values(edgesByRelation).forEach((edges) => {
      edges.sort((a, b) => b.attention - a.attention);
    });

    const drugName = nodeNameDict['drug']?.[selectedDrugPrimary] || selectedDrugPrimary;
    const diseaseName = selectedDisease.name || selectedDisease.id;

    return (
      <div style={{ width, height, overflow: 'auto', padding: '10px' }}>
        {/* Info Box */}
        <div
          style={{
            background: '#e7f3ff',
            borderLeft: '4px solid #3a7bd5',
            padding: '15px',
            marginBottom: '15px',
            borderRadius: '5px',
          }}
        >
          <strong>Total Edges:</strong> {subgraph.edges.length} |{' '}
          <strong>Relation Types:</strong> {Object.keys(edgesByRelation).length}
          <br />
          <small>
            Showing attention scores for: <strong>{drugName}</strong>  <strong>{diseaseName}</strong>
          </small>
        </div>

        {/* Tables by Relation */}
        {Object.entries(edgesByRelation).map(([relation, edges]) => (
          <div key={relation} style={{ marginBottom: '20px' }}>
            <h4
              style={{
                color: '#3a7bd5',
                marginTop: '15px',
                marginBottom: '10px',
                fontSize: '14px',
                fontWeight: 600,
              }}
            >
              {relation.replace(/_/g, '  ')} ({edges.length})
            </h4>
            <table
              style={{
                width: '100%',
                borderCollapse: 'collapse',
                fontSize: '13px',
              }}
            >
              <thead>
                <tr style={{ background: '#f8f9fa' }}>
                  <th
                    style={{
                      padding: '10px',
                      textAlign: 'left',
                      borderBottom: '2px solid #dee2e6',
                      fontWeight: 600,
                      color: '#495057',
                    }}
                  >
                    Source
                  </th>
                  <th
                    style={{
                      padding: '10px',
                      textAlign: 'left',
                      borderBottom: '2px solid #dee2e6',
                      fontWeight: 600,
                      color: '#495057',
                    }}
                  >
                    Target
                  </th>
                  <th
                    style={{
                      padding: '10px',
                      textAlign: 'center',
                      borderBottom: '2px solid #dee2e6',
                      fontWeight: 600,
                      color: '#495057',
                      width: '80px',
                    }}
                  >
                    Layer 1
                  </th>
                  <th
                    style={{
                      padding: '10px',
                      textAlign: 'center',
                      borderBottom: '2px solid #dee2e6',
                      fontWeight: 600,
                      color: '#495057',
                      width: '80px',
                    }}
                  >
                    Layer 2
                  </th>
                  <th
                    style={{
                      padding: '10px',
                      textAlign: 'left',
                      borderBottom: '2px solid #dee2e6',
                      fontWeight: 600,
                      color: '#495057',
                      width: '150px',
                    }}
                  >
                    Max Attention
                  </th>
                </tr>
              </thead>
              <tbody>
                {edges.slice(0, 20).map((edge, idx) => {
                  const sourceNode = nodeMap[edge.source];
                  const targetNode = nodeMap[edge.target];
                  const maxAtt = Math.max(edge.layer1_att || 0, edge.layer2_att || 0);

                  return (
                    <tr
                      key={idx}
                      style={{
                        borderBottom: '1px solid #e9ecef',
                      }}
                      onMouseEnter={(e) => {
                        (e.currentTarget as HTMLElement).style.backgroundColor = '#f8f9fa';
                      }}
                      onMouseLeave={(e) => {
                        (e.currentTarget as HTMLElement).style.backgroundColor = 'transparent';
                      }}
                    >
                      <td style={{ padding: '10px' }}>
                        <strong>{sourceNode?.label || edge.source}</strong>
                        <br />
                        <small style={{ color: '#6c757d' }}>
                          ({this.formatType(sourceNode?.type || 'unknown')})
                        </small>
                      </td>
                      <td style={{ padding: '10px' }}>
                        <strong>{targetNode?.label || edge.target}</strong>
                        <br />
                        <small style={{ color: '#6c757d' }}>
                          ({this.formatType(targetNode?.type || 'unknown')})
                        </small>
                      </td>
                      <td
                        style={{
                          padding: '10px',
                          textAlign: 'center',
                        }}
                      >
                        {(edge.layer1_att || 0).toFixed(4)}
                      </td>
                      <td
                        style={{
                          padding: '10px',
                          textAlign: 'center',
                        }}
                      >
                        {(edge.layer2_att || 0).toFixed(4)}
                      </td>
                      <td style={{ padding: '10px' }}>
                        {this.renderAttentionBar(maxAtt)}
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            </table>
            {edges.length > 20 && (
              <div
                style={{
                  textAlign: 'center',
                  padding: '10px',
                  color: '#6c757d',
                  fontSize: '12px',
                }}
              >
                Showing top 20 of {edges.length} edges
              </div>
            )}
          </div>
        ))}
      </div>
    );
  }
}

