import {
  DrugPrediction,
  IDispatch,
  IPath,
  IMetaPath,
  IMetaPathSummary,
} from 'types';
import {
  requestAttentionPair,
  requestDrugPredictions,
  requestDiseasePredictions,
} from 'stores/DataService';

export const ACTION_TYPES = {
  Load_Node_Types: 'Load_Node_Types',
  Load_Edge_Types: 'Load_Edge_Types',
  Load_Meta_Paths: 'Load_Meta_Paths',
  Add_Attention_Paths: 'Add_Attention_Paths',
  Del_Attention_Paths: 'Del_Attention_Paths',
  Load_Node_Name_Dict: 'Load_Node_Name_Dict',
  Load_Drug_Options: 'Load_Drug_Options',
  Load_Disease_Options: 'Load_Disease_Options',
  Load_Disease_Predictions: 'Load_Disease_Predictions',
  Load_Drug_List: 'Load_Drug_List',

  Set_Loading_Status: 'Set_Loading_Status',

  Change_Edge_THR: 'Change_Edge_THR',
  Change_Drug: 'Change_Drug',
  Change_Disease: 'Change_Disease',
  Change_App_Mode: 'Change_App_Mode',
  Change_Selected_Drug_Primary: 'Change_Selected_Drug_Primary',
  Select_Path_Noes: 'Select_Path_Nodes',

  Toggle_Meta_Path_Hide: 'Toggle_Meta_Path_Hide',
  Toggle_Disease_Selection: 'Toggle_Disease_Selection',
};

export const selectDrug = (
  selectedDrug: string,
  selectedDisease: string | undefined,
  isAdd: boolean,
  dispatch: IDispatch
) => {
  if (selectedDisease) {
    modifyAttentionPaths(selectedDrug, selectedDisease, isAdd, dispatch);
    changeDrug(selectedDrug, dispatch);
  }
};

export const selectDisease = (selectedDisease: string, dispatch: IDispatch) => {
  dispatch({
    type: ACTION_TYPES.Set_Loading_Status,
    payload: { isDrugLoading: true },
  });

  dispatch({
    type: ACTION_TYPES.Change_Disease,
    payload: { selectedDisease },
  });

  dispatch({
    type: ACTION_TYPES.Change_Drug,
    payload: { selectedDrug: undefined },
  });

  return requestDrugPredictions(selectedDisease)
    .then((res) => {
      const predictions = res.predictions;
      const drugPredictions = predictions.map((d: DrugPrediction) => {
        return { ...d, selected: false };
      });
      dispatch({
        type: ACTION_TYPES.Load_Drug_Options,
        payload: { drugPredictions },
      });
    })
    .then(() => {
      dispatch({
        type: ACTION_TYPES.Set_Loading_Status,
        payload: { isDrugLoading: false },
      });
    });
};

export const selectDrugPrimary = (selectedDrug: string, dispatch: IDispatch) => {
  dispatch({
    type: ACTION_TYPES.Set_Loading_Status,
    payload: { isDiseaseLoading: true },
  });

  dispatch({
    type: ACTION_TYPES.Change_Selected_Drug_Primary,
    payload: { selectedDrugPrimary: selectedDrug },
  });

  // Clear previous selections
  dispatch({
    type: ACTION_TYPES.Change_Disease,
    payload: { selectedDisease: undefined },
  });

  // Fetch disease predictions
  return requestDiseasePredictions(selectedDrug)
    .then((res) => {
      const predictions = res.predictions;
      const diseasePredictions = predictions.map((d: DrugPrediction) => {
        return { ...d, selected: false };
      });
      dispatch({
        type: ACTION_TYPES.Load_Disease_Predictions,
        payload: { diseasePredictions },
      });
    })
    .then(() => {
      dispatch({
        type: ACTION_TYPES.Set_Loading_Status,
        payload: { isDiseaseLoading: false },
      });
    });
};

export const changeAppMode = (mode: 'disease-to-drug' | 'drug-to-disease', dispatch: IDispatch) => {
  dispatch({
    type: ACTION_TYPES.Change_App_Mode,
    payload: { appMode: mode },
  });

  // Load drug list if switching to drug-to-disease and list is empty
  if (mode === 'drug-to-disease') {
    fetch(`http://127.0.0.1:8002/api/drugs`)
      .then((response) => response.json())
      .then((drugs) => {
        dispatch({
          type: ACTION_TYPES.Load_Drug_List,
          payload: { drugOptions: drugs },
        });
      });
  }
};

export const selectDiseaseSecondary = (
  selectedDisease: string,  // disease_id for selection toggle
  selectedDrugPrimary: string,
  isAdd: boolean,
  dispatch: IDispatch,
  diseaseNodeId?: string  // Optional: node_id for attention API (defaults to selectedDisease)
) => {
  const nodeIdForAttention = diseaseNodeId || selectedDisease;
  
  // Toggle disease selection state (uses disease_id)
  dispatch({
    type: ACTION_TYPES.Toggle_Disease_Selection,
    payload: { diseaseId: selectedDisease },
  });

  // Update selected disease in state
  dispatch({
    type: ACTION_TYPES.Change_Disease,
    payload: { selectedDisease: nodeIdForAttention },
  });

  // Ensure primary drug is set as selectedDrug for attention paths
  dispatch({
    type: ACTION_TYPES.Change_Drug,
    payload: { selectedDrug: selectedDrugPrimary },
  });

  // Use node_id for attention API, disease_id as the groupKey
  modifyAttentionPaths(selectedDrugPrimary, nodeIdForAttention, isAdd, dispatch, selectedDisease);
};

const modifyAttentionPaths = (
  selectedDrug: string | undefined,
  selectedDisease: string | undefined,
  isAdd: boolean,
  dispatch: IDispatch,
  key?: string // Optional key to use for metaPathGroups (defaults to selectedDrug)
) => {
  const groupKey = key || selectedDrug;
  if (selectedDrug !== undefined && selectedDisease !== undefined && groupKey !== undefined) {
    if (isAdd) {
      dispatch({
        type: ACTION_TYPES.Set_Loading_Status,
        payload: { isAttentionLoading: true },
      });

      requestAttentionPair(selectedDisease, selectedDrug)
        .then((res) => {
          dispatch({
            type: ACTION_TYPES.Add_Attention_Paths,
            payload: {
              attention: res.attention,
              selectedDrug: groupKey, // Use groupKey as the identifier for adding/removing
              metaPathGroups: { [groupKey]: groupMetaPaths(res.metapaths) },
            },
          });
        })
        .then(() => {
          dispatch({
            type: ACTION_TYPES.Set_Loading_Status,
            payload: { isAttentionLoading: false },
          });
        });
    } else {
      dispatch({
        type: ACTION_TYPES.Del_Attention_Paths,
        payload: {
          selectedDrug: groupKey,
        },
      });
    }
  }
};

const changeDrug = (selectedDrug: string, dispatch: IDispatch) => {
  dispatch({
    type: ACTION_TYPES.Change_Drug,
    payload: { selectedDrug },
  });
};

const groupMetaPaths = (paths: IPath[]): IMetaPath[] => {
  let groups: IMetaPath[] = [];
  let groupDict: string[] = [];
  paths.forEach((path) => {
    path.hide = false; // initi, show all metapaths
    const nodeTypeString = path.nodes.map((d) => d.nodeType).join('_');
    const groupIdx = groupDict.indexOf(nodeTypeString);
    if (groupIdx > -1) {
      groups[groupIdx].paths.push(path);
    } else {
      groupDict.push(nodeTypeString);
      groups.push({
        nodeTypes: path.nodes.map((d) => d.nodeType),
        paths: [path],
      });
    }
  });
  return groups;
};

export const toggleMetaPathHide = (
  metaPathSummary: IMetaPathSummary[],
  idx: number,
  hide: boolean,
  dispatch: IDispatch
) => {
  metaPathSummary[idx]['hide'] = hide;
  dispatch({
    type: ACTION_TYPES.Toggle_Meta_Path_Hide,
    payload: { metaPathSummary },
  });
};
